	module.exports = {"data":{"markdownRemark":{"html":"<h3>Day 1</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>1. Thinking Like a Programmer</h4>\n<ul>\n<li>Basic Problem Solving</li>\n<li>Puzzle Solving, looking for possible solutions</li>\n<li>Breaking down a Problem</li>\n<li>Listing Instructions</li>\n<li>Deconstructing word problems</li>\n</ul>\n</div>\n<div class=\"column\">\n<h4>2. HTML Part 1</h4>\n<ul>\n<li>\n<p>HTML 5</p>\n</li>\n<li>\n<p>Tags &#x26; Elements</p>\n</li>\n<li>\n<p>Document Structure</p>\n</li>\n<li>\n<p>Tags and Their Attributes</p>\n  </div>\n</div>\n</li>\n</ul>\n<hr>\n<h3>Day 2</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>3. HTML Part 2</h4>\n<ul>\n<li>Semantic Tags (Header, Body, Footer)</li>\n<li>Outlining a Doc with Tags</li>\n<li>Nav, Table Elements</li>\n<li>Forms and Input Elements</li>\n<li>Submitting The Form</li>\n</ul>\n</div>\n<div class=\"column\">\n<h4>4. CSS Part 1</h4>\n<ul>\n<li>\n<p>What is CSS</p>\n</li>\n<li>\n<p>CSS Syntax</p>\n</li>\n<li>\n<p>Basic Properties and Values</p>\n</li>\n<li>\n<p>Selectors</p>\n</li>\n<li>\n<p>Pseudo-classes</p>\n</li>\n<li>\n<p>CSS File w/ <code>&#x3C;link></code> tag</p>\n  </div>\n</div>\n</li>\n</ul>\n<hr>\n<h3>Day 3</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>5. CSS Part 2</h4>\n<ul>\n<li>Expanding on Properties</li>\n<li>Styling Text</li>\n<li>CSS Border</li>\n<li>Displaying Content</li>\n<li>CSS Box Model</li>\n<li>Margin v Padding</li>\n<li>Placing Content / Layout</li>\n</ul>\n</div>\n<div class=\"column\">\n<h4>6. JavaScript Part 1</h4>\n<ul>\n<li>\n<p>JavaScript’s role in Webpages</p>\n</li>\n<li>\n<p>JavaScript vs Java</p>\n</li>\n<li>\n<p>Script Tags &#x26; Linking *.js files</p>\n</li>\n<li>\n<p>Types</p>\n</li>\n<li>\n<p>Variables</p>\n</li>\n<li>\n<p>Conditional <code>if else</code> statements</p>\n</li>\n<li>\n<p>Comparison <a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/Comparison_Operators\">Operators</a></p>\n </div>\n</div>\n</li>\n</ul>\n<hr>\n<h3>Day 4</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>7. JavaScript Part 2</h4>\n<ul>\n<li>Arrays</li>\n<li>Array Properties and Methods</li>\n<li>Loops!</li>\n<li>For Loops.</li>\n<li>Looping Through Array</li>\n</ul>\n</div>\n<div class=\"column\">\n<h4>8. JavaScript Part 3</h4>\n<ul>\n<li>\n<p>Functions. Structor and Syntax</p>\n</li>\n<li>\n<p>Calling or Invoking a Function</p>\n</li>\n<li>\n<p>Scope, Global / Local</p>\n</li>\n<li>\n<p>Document Object Model (DOM)</p>\n</li>\n<li>\n<p>Retrieving DOM Elements</p>\n</li>\n<li>\n<p>Manipulating DOM Elments</p>\n</li>\n<li>\n<p>HTML Event Handling</p>\n  </div>\n</div>\n</li>\n</ul>\n<hr>\n<h3>Day 5</h3>\n<div class=\"row\">\n<div class=\"column\">\n<h4>9. The Terminal &#x26; Source Control</h4>\n<ul>\n<li>Command Line Interface (CLI)</li>\n<li>Commands parameters / Paths</li>\n<li>Showing files and changing directories</li>\n<li>Source Control Git</li>\n<li>Commits, Repos, Github, Branches</li>\n<li>Local v Remote Repos</li>\n</ul>\n</div>\n<div class=\"column\">\n<h4>10. Final Project CFS</h4>\n<ul>\n<li>\n<p>Coding From Scratch, you will be building a form, use the data inputted in the form to create a basic ID card.\n<img src=\"https://resourceapi.qa.exeterlms.com/Resources/76ccea28-da1c-49c2-83bd-fb9b5e991738\"></p>\n</div>\n</div>\n</li>\n</ul>\n<hr>","headings":[{"depth":3,"value":"Day 1"},{"depth":4,"value":"1. Thinking Like a Programmer"},{"depth":4,"value":"2. HTML Part 1"},{"depth":3,"value":"Day 2"},{"depth":4,"value":"3. HTML Part 2"},{"depth":4,"value":"4. CSS Part 1"},{"depth":3,"value":"Day 3"},{"depth":4,"value":"5. CSS Part 2"},{"depth":4,"value":"6. JavaScript Part 1"},{"depth":3,"value":"Day 4"},{"depth":4,"value":"7. JavaScript Part 2"},{"depth":4,"value":"8. JavaScript Part 3"},{"depth":3,"value":"Day 5"},{"depth":4,"value":"9. The Terminal & Source Control"},{"depth":4,"value":"10. Final Project CFS"}],"frontmatter":{"title":"Coding From Scratch"}}},"pathContext":{"slug":"/courses/coding-from-scratch/syllabus/"}}

